.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.glass-div {
  background-color: rgba(173, 216, 230, 0.4); /* Transparent white background */
  border-radius: 10px;
  padding: 20px;
  backdrop-filter: blur(10px); /* Creates the glass effect */
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.3), -10px 10px 10px rgba(0, 0, 0, 0.1); /* Inner shadow effect and left bottom shadow */
  border: 2px solid rgba(30, 144, 255, 0.3); /* Light blue border */
  /* Shining effect on hover */
  transition: box-shadow 0.5s ease;
}

.narrow-div {
  width: 80%;
}

.glass-div:hover {
  box-shadow: 0 0 20px 5px rgba(255, 255, 255, 0.7); /* Increased shadow size and opacity */
}

.content-section {
  padding-top: 70px; 
}





@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
